*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  height: 100vh;
  background-color: rgb(225, 225, 230);
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}

/*
 board width: 3.4em
  content = 3 * (cells 1.6em) = 4.8em
  board border = 2 * .2em = .4em
  board(em) = 5.2em
  
  side bar = 1.6em
  overall width(em) = 6.8em;

  app: 6.8em
  app margin left: 0.8em(1/2 of sidebar width)

  app height(large Screen):

  board: 5.2em + 2px (@fontsize: 100px -> 522px)
  header: 72px (vert margins + border-box height)
  footer: 60px (top margin + border box height)

  overall height: 5.2em + 134px -> 654px
*/

#app {
  font-family: Roboto, sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
}

#app > *,
main > * {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  position: relative;
}

header {
  width: 5.2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  margin: 0.2em 0;
}

ul {
  list-style: none;
}

kbd {
  display: inline-block;
  font-family: monospace;
  font-size: 0.8em;
  padding: 0 0.4em;
  vertical-align: text-bottom;
  background-color: rgba(146, 162, 189, 0.5);
  border-radius: 4px;
  border-right: 2px solid rgba(146, 162, 189, 1);
  border-bottom: 2px solid rgba(146, 162, 189, 1);
}

h1 {
  color: rgb(97, 117, 146);
  text-align: center;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

h2 {
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
  margin-bottom: 0.5em;
}

h3 {
  font-size: 1em;
  text-align: left;
  margin: 0.5em 0;
}

section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

section > p {
  line-height: 1.2em;
}

section + section {
  margin-top: 0.8em;
}

@media only screen and (min-device-width: 769px) {
  #app {
    /*the dimensions of this app scale with this property*/
    font-size: 100px;
  }
  h1 {
    font-size: 0.6em;
    margin-right: 0.1em;
  }
}

@media only screen and (min-device-width: 769px) {
  #app {
    font-size: 11.5vh;
  }
  h1 {
    font-size: 0.6em;
    margin-right: 0.1em;
  }
}

@media only screen and (max-device-width: 768px) {
  #app {
    font-size: 15.5vmin;
    width: 100%;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  h1 {
    font-size: 0.95em;
    margin-right: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  main {
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
  }
  header {
    width: 5.2em;
    padding-right: 0;
  }
}

@media only print {
  h1 {
    color: black;
  }
  header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width: 5.2em;
    padding-right: 0;
  }
  body {
    background-color: transparent;
  }
}

.keybindings-list {
  list-style: disc outside;
}

.keybindings-list > li + li {
  margin-top: 5px;
}

/*removes the ugly dotted outline in firefox*/

button::-moz-focus-inner {
  border: 0;
}

button {
  font-family: inherit;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@-webkit-keyframes slideOutLeft {
  from {
    left: 0;
  }
  to {
    left: -2.8em;
  }
}

@keyframes slideOutLeft {
  from {
    left: 0;
  }
  to {
    left: -2.8em;
  }
}

@-webkit-keyframes slideInLeft {
  from {
    left: -2.8em;
  }
  to {
    left: 0;
  }
}

@keyframes slideInLeft {
  from {
    left: -2.8em;
  }
  to {
    left: 0;
  }
}

@-webkit-keyframes zoomOut {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
}

@keyframes zoomOut {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
}

@-webkit-keyframes increaseSize {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes increaseSize {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  to {
    -webkit-transform: rotateX(1440deg);
            transform: rotateX(1440deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  to {
    -webkit-transform: rotateX(1440deg);
            transform: rotateX(1440deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}



