.listbox {
  display: inline-block;
  position: relative;
  font-size: 0.2em;
  width: 6em;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  cursor: pointer;
  /*fix unclickable divs in safari*/
  background-color: transparent;
  /*without it firefox mobile and desktop forces the selection of 
  all the option items on focus!*/
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.listbox-theme {
  outline: none;
}

.listbox-theme:focus[aria-expanded="true"],
.listbox-theme:active[aria-expanded="true"] {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.listbox * {
  font-size: inherit;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.listbox > div {
  pointer-events: none;
}

.listbox-button {
  width: 100%;
  z-index: 40;
  display: -ms-flexbox;
  display: flex;
  padding: 0.4em;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.listbox-button-theme {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  color: rgba(26, 26, 26, 1);
  background-color: rgb(146, 162, 189);
}

.listbox-button > span {
  display: block;
  width: 4.5em;
  text-align: center;
}

.listbox-theme:focus > .listbox-button-theme,
.listbox-theme:active > .listbox-button-theme {
  background-color: rgb(159, 177, 207);
  -webkit-box-shadow: 0px 3px 3px -1px rgba(10, 10, 10, 0.5);
          box-shadow: 0px 3px 3px -1px rgba(10, 10, 10, 0.5);
  outline: none;
}

.option-list {
  position: absolute;
  left: 0;
  top: 56%;
  z-index: 22;
  width: 6em;
  margin: 0;
  -webkit-transition: opacity 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  overflow-y: hidden;
}

.option-list-theme {
  border: 1px solid grey;
  border-radius: 4px;
  background-color: rgb(240, 240, 240);
}

.option-list-visible {
  display: block;
  opacity: 1;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
}

.svg-animator {
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  -o-transition: transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.rotate-90 {
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.option-item {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  width: 100%;
  padding: 5% 3%;
  cursor: pointer;
  /*fix unclickable divs in safari*/
}

.option-item > span {
  display: inline-block;
  height: 1em;
}

.option-item-theme {
  border-width: 0;
  background-color: transparent;
}

.option-item:first-child {
  margin-top: 0.8em;
}

.option-item-theme[aria-selected="true"] {
  background-color: hsl(219, 50%, 75%);
}

.option-item > span:first-child {
  display: inline-block;
  text-align: center;
}

.option-item > span:last-child {
  width: 4.5em;
}

@media only screen and (min-device-width: 769px) {
  .option-item-theme:hover {
    background-color: rgb(215, 215, 215);
  }
  .option-list-hidden {
    opacity: 0;
  }
  .option-list-anim-over {
    display: none;
    opacity: 0;
  }
}

@media only screen and (max-device-width: 768px) {
  .listbox {
    font-size: 0.35em;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .listbox-theme:focus > .listbox-button-theme,
  .listbox-theme:active > .listbox-button-theme {
    -webkit-box-shadow: none;
            box-shadow: none;
  }

  .option-item {
    padding: 10% 3%;
  }
  /*reduce animations on mobile screens*/
  .option-list-hidden {
    display: none;
  }
  .option-list-anim-over {
    display: none;
  }
}

@media print {
  .listbox-button-theme {
    background-color: transparent;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .listbox-button-theme > div:first-child {
    display: none;
  }
  .option-list {
    display: none;
  }
}



